//																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																													the reducer allows us to update our initial state 
//however it is  updates a copy of the state and not the real state

//so it has initial state whose copy is updated based on the 
//action type

const initialState ={
	fullnames:'',
	phonenumber:'',
	birthDate:'',
	email:''
}

export default (state=initialState,action)=>{
	switch(action.type){
		case 'stepseven':
		//console.log('got it')
		return {
			...state,
			fullnames:action.payload.fullnames,
			phonenumber:action.payload.phonenumber,
			birthDate:action.payload.birthDate,
			email:action.payload.email
		}
		case 'reset':
		return{
			...state,
			fullnames:action.payload,
			phonenumber:action.payload,
			birthDate:action.payload,
			email:action.payload
		}
		default:
		return state
	}
}